  z-index: 2;
}
.header__wrap {
  padding: 14px 0 17px;
  display: flex;
  align-items: center;
  position: relative;
}
.header .logo {
  max-width: 218px;
  display: inline-block;
}
.header__menu {
  display: flex;
  align-items: center;
  margin: -6px 0 0 153px;
  padding: 0;
  list-style: none;
  font-size: 14px;
  font-weight: 700;
  color: #ffffff;
}
.header__menu-item {
  position: relative;
  line-height: 1;
}
.header__menu-item:not(:last-of-type) {
  margin-right: 47px;
}
.header__menu-drop {
  position: absolute;
  top: -1px;
  right: -15px;
  width: 15px;
  height: 15px;
  background-image: url('../img/icons/menu-arrow.png');
  background-size: 7px 5px;
  background-position: center;
  background-repeat: no-repeat;
  cursor: pointer;
  transform: rotate(0);
  transition: 0.3s;
}
.header__menu-drop.active {
  transform: rotate(0);
}
.header__menu-sub {
  display: none;
  position: absolute;
  z-index: 2;
  width: 175px;
  top: 100%;
  left: -50%;
