  margin: 0;
  padding: 20px 30px;
  list-style: none;
  font-size: 13px;
}
.header__contacts {
  display: flex;
  align-items: center;
  margin: -6px 0 0 auto;
}
.header__contacts-item {
  font-size: 14px;
  font-weight: 700;
  color: #ffffff;
}
.header__contacts-item:not(:last-child) {
  margin-right: 46px;
}
.header .burger {
  width: 40px;
  height: 40px;
  border-radius: 100%;
  position: relative;
  display: none;
  margin-left: 22px;
}
.header .burger span {
  width: 20px;
  height: 2px;
  background-color: #ffffff;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}
.header .burger span:nth-of-type(1) {
  bottom: 12px;
}
.header .burger span:nth-of-type(3) {
  top: 12px;
}
.header .search-block {
  display: none;
  position: fixed;
  z-index: 2;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #ffffff;
  padding: 40px;
}
.header_mainpage {
  position: absolute!important;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 2;
  border: none;
  background: none;
}
.header_prod {
  border: none;
}
