@media screen and (max-width: 768px) {
  .footer__menu {
    flex-wrap: wrap;
  }
  .footer__menu-item:not(:last-of-type) {
    margin-right: 30px;
  }
  .footer__contacts {
    flex-wrap: wrap;
  }
