.feedback__wrap {
  display: flex;
  margin: 0 -25px;
}
.feedback__col {
  margin: 0 25px;
}
.feedback__col:first-of-type {
  width: calc(40% - 50px);
}
.feedback__col:last-of-type {
  width: calc(60% - 50px);
}
.feedback__item {
  width: 100%;
  display: block;
}
.feedback__item:not(:last-of-type) {
  margin-bottom: 16px;
}
.feedback__item-name {
  font-size: 17px;
  margin: 0 0 8px;
  line-height: 1;
}
.feedback__item-input {
  width: 100%;
  height: 50px;
  border: 1px solid #c8d6ed;
  padding: 0 20px;
  color: #0058a2;
}
.feedback__item-comment {
  width: 100%;
  height: 138px;
  border: 1px solid #c8d6ed;
  padding: 20px;
  color: #0058a2;
}
.feedback__footer {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-top: 37px;
}
.feedback__agree {
  position: relative;
  display: block;
  cursor: pointer;
}
.feedback__agree .agree {
  opacity: 0;
  visibility: hidden;
  position: absolute;
}
.feedback__agree .agree:checked + .feedback__agree-text:after {
  opacity: 1;
}
.feedback__agree-text {
  position: relative;
  font-size: 13px;
  padding-left: 39px;
}
.feedback__agree-text:before {
  content: '';
  position: absolute;
  left: 0;
  top: -9px;
  width: 30px;
  height: 30px;
  border: 1px solid #c8d6ed;
}
.feedback__agree-text:after {
  content: '';
  position: absolute;
  top: 0px;
  left: 7px;
  width: 16px;
  height: 12px;
  background-image: url("../img/icons/tick.png");
  background-size: cover;
  opacity: 0;
  transition: 0.3s;
}
.feedback__agree-text a {
  text-decoration: underline;
}
.feedback__submit {
  color: #ffffff;
  background-color: #e41b43;
  border-radius: 100px;
  border: 1px solid #e41b43;
  font-size: 14px;
  font-weight: 700;
  display: inline-block;
  padding: 10px 39px 10px 15px;
  position: relative;
  margin: -7px 28px 0 0;
}
.feedback__submit:after {
  position: absolute;
  content: '';
  width: 20px;
  height: 16px;
  background-image: url("../img/icons/mail.png");
  background-size: cover;
  top: 0;
  right: 12px;
  bottom: 0;
  margin: auto;
}
