}
.slider .wrapper {
  height: 100%;
}
.slider-pag {
  position: absolute;
  width: 100%;
  bottom: 133px;
  z-index: 2;
}
.slider-pag__wrap {
  display: flex;
  align-items: center;
  width: 45%;
  margin-left: auto;
  padding-left: 57px;
}
.slider-pag .arrow {
  cursor: pointer;
  width: 26px;
  height: 26px;
  display: flex;
  align-items: center;
}
.slider-pag .arrow img {
  width: 26px;
}
.slider-pag .dots {
  margin: 0 6px;
}
.slider-pag .slick-dots {
  position: static;
  display: flex;
  align-items: center;
}
.slider-pag .slick-dots li {
  position: relative;
  width: 42px;
  height: 42px;
  margin: 0 5px;
}
.slider-pag .slick-dots li.slick-active .dots-svg #bar {
  animation: circle 6s linear forwards;
  stroke-dasharray: 270;
}
.slider-pag .slick-dots li.slick-active span:before {
  background-color: #e41b43;
  width: 85%;
  height: 85%;
}
.slider-pag .slick-dots .dots-svg {
  position: absolute;
  left: 0;
  top: 0;
  transform: rotate(-90deg);
}
.slider-pag .slick-dots .dots-svg #bar {
  stroke-dasharray: 140;
  stroke-linecap: round;
}
.slider-pag .slick-dots span {
  display: block;
  width: 32px;
  height: 32px;
  font-size: 15px;
  font-weight: 700;
  color: #ffffff;
